  transition: 0.5s background-color;
  position: relative;
  z-index: 1;
  transition: 0.3s color;
}
#btn-sidebar1 {
  background-color:#d8202b;
  border: none;
  padding: 0.6rem 1.3rem;
  color: #f0f0f0;
  font-weight: 200;
  font-size: 1.3rem;
  cursor: pointer;
  position: relative;
  z-index: 1;
  border-radius: 0.5rem;
}
#btn-sidebar::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 0%;
  background-color: #005c99;
  z-index: -1;
  border-radius: 1rem;
  transition: 0.5s width;
}

#btn-sidebar:hover {
  color: whitesmoke;
}

#btn-sidebar:hover::after {
  width: 100%;
}

