.contacts {
  width: 100%;
  height: 30rem;
  background-color: #f5f5f5;
  margin-top: 7rem;
}
.contact-head {
  text-align: center;
  padding-top: 3rem;
  font-size: 2.3rem;
}

.icon-area{
    margin-top: 3.5rem;
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    margin-left: -7rem;
    width: 100vw;
}
.contact-icon, .phone1-icon, .mail-icon{
    margin-left: 18rem;
}
.icon-content{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    margin-left: -13rem;
    margin-top: 1.5rem;
}
.address-head, .phone-head, .email-head {
    margin-left: 17.5rem;
    color: #1a2550;
}
.main-icon-content{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    margin-left: 5rem;
    margin-top: 1.3rem;
}
.phone-body, .email-body{
    margin-left: 12rem;
}