.side-main {
  display: flex;
  flex-direction: row;
  background-color: whitesmoke;
  justify-content: space-around;
  height: 5rem;
  align-items: center;
}

#heading {
  text-decoration: none;
  color: #262626;
  font-size: 1.2rem;
}

ul {
  display: flex;
}

li {
  list-style: none;
  margin-left: 0rem;
}
.btn-items{
  margin-left: 2.5rem;
}

#btn-sidebar {
  border: none;
  background-color: whitesmoke;
  padding: 0.6rem 1rem;
  color: #1a2550;
  font-weight: 200;
  font-size: 1.3rem;
  cursor: pointer;
  transition: 0.5s background-color;
  position: relative;
  z-index: 1;
  transition: 0.3s color;
}
#btn-sidebar1 {
  background-color:#d8202b;
  border: none;
  padding: 0.6rem 1.3rem;
  color: #f0f0f0;
  font-weight: 200;
  font-size: 1.3rem;
  cursor: pointer;
  position: relative;
  z-index: 1;
  border-radius: 0.5rem;
}
#btn-sidebar::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 0%;
  background-color: #005c99;
  z-index: -1;
  border-radius: 1rem;
  transition: 0.5s width;
}

#btn-sidebar:hover {
  color: whitesmoke;
}

#btn-sidebar:hover::after {
  width: 100%;
}

/*************** main css *********************/
.img-2 {
  width: 100%;
  height: 20%;
  margin-top: 1rem;
  margin: auto;
  position: relative;
}

.slider-img {
  display: block;
  width: 100%;
  height: 42rem;
}

.left-hover {
  width: 50%;
  height: 100%;
  background-color: rgba(34, 49, 63, 0.8);
  position: absolute;
  top: 0;
  overflow: hidden;
  opacity: 0.8;
  animation-name: hover;
  animation-duration: 4s;
}
@keyframes hover {
  from {
    width: 0;
  }
  to {
    width: 50%;
  }
}
.hover-text {
  position: absolute;
  top: 10rem;
  left: 15rem;
  font-size: 0rem;
  color: #ffffff;
  font-family: lato;
  letter-spacing: 0.2rem;
  word-spacing: 0.4rem;
  font-weight: 100;
  text-transform: uppercase;
  animation-name: increase1;
  animation-duration: 4s;
  animation-delay: 3s;
  animation-fill-mode: forwards;
}
.heading-2 {
  margin-top: 3rem;
  color: lightskyblue;
}

@keyframes increase1 {
  from {
    font-size: 0rem;
  }
  to {
    font-size: 1.3rem;
  }
}

.list-btn-2 {
  position: absolute;
  top: 16rem;
  left: 4rem;
}

.btn-item-2 {
  color: #f0f0f0;
  background-color: #d8202b;
  box-shadow: none;
  border: none;
  cursor: pointer;
  padding: 1rem 2rem;
  font-size: 1.3rem;
  font-weight: bold;
  animation-name: name;
  animation-duration: 4s ease-out;
  animation-delay: 3s;
  animation-fill-mode: forwards;
  transform: scale(0);
  border-radius: 0.5rem;
}
@keyframes name {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
