/********************* Media Query For Header ***********************/

@media screen and (max-width: 750px) {
  .side-main {
    flex-direction: column;
    height: 8rem;
  }

  .heading-cls {
    border-bottom: 0.2rem solid #005c99;
    width: 100%;
    text-align: center;
    height: 4rem;
    margin-top: -2rem;
  }

  ul {
    margin-left: -4.5rem;
    height: 1rem;
    margin-top: -3.5rem;
  }

  li {
    margin-left: 5rem;
  }
}

@media screen and (max-width: 540px) {
  #heading {
    font-size: 1rem;
  }

  li {
    margin-left: 2.5rem;
  }
}

@media screen and (max-width: 440px) {
  .side-main {
    height: 8rem;
  }
  #heading {
    font-size: 0.8rem;
  }
  ul {
    margin-left: -2rem;
  }
  li {
    margin-left: 2rem;
  }
  #btn-sidebar {
    font-size: 1rem;
  }
}

@media screen and (max-width: 340px) {
  #heading {
    font-size: 1rem;
  }

  li {
    margin-left: 0rem;
  }
  ul {
    margin-left: -0.5rem;
  }
}

/************************ Main Css  Media Query ********************/

@media screen and (max-width: 1150px) {
  .text {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 750px) {
  .photo {
    height: 27rem;
  }
}

@media screen and (max-width: 650px) {
  .text {
    font-size: 1.5rem;
  }
  .photo {
    height: 50vh;
  }
}

@media screen and (max-width: 550px) {
  .text {
    font-size: 1rem;
  }
  .btn-main {
    font-size: 0.8rem;
    padding: 0.7rem 1.5rem;
  }
}

@media screen and (max-width: 450px) {
  .photo {
    height: 40vh;
  }
}
